
import React, {memo, useState} from "react";
import '../../images/FunnyCook/funnyCook.css'
import img_Rightbuttionpush from "../../images/FunnyCook/Rightbuttionpush.png";
import img_Leftbuttionpush from "../../images/FunnyCook/Leftbuttionpush.png";
import img_ButtionA from "../../images/FunnyCook/ButtionA.png";

function Controls(props){
    // console.log("render Controls")

    const [Rightbuttionpush, setRightbuttionpush] = useState(false);
    const [Leftbuttionpush, setLeftbuttionpush] = useState(false);

    const callbackR = props.callbackR
    const callbackL = props.callbackL

    const clickGameA = props.clickGameA
    const ButtionA = props.gameA

    const mouseDownHandlerR = () => {
        setRightbuttionpush( true);
        callbackR()
        console.log("mouseDownHandlerR")
    }
    const mouseUpHandlerR = () => {
        setRightbuttionpush( false);
        console.log("mouseUpHandlerR")
    }

    const mouseDownHandlerL = () => {
        callbackL()
        setLeftbuttionpush( true);
    }
    const mouseUpHandlerL = () => {
        setLeftbuttionpush( false);
    }
    return (
        <div>
            <div className="Rightbuttionpush"
                  onMouseDown={mouseDownHandlerR}
                  onMouseUp={mouseUpHandlerR}>
                {(Rightbuttionpush  || props.buttonRDown) && <img src={img_Rightbuttionpush}/>} </div>

            <div className="Leftbuttionpush"
                  onMouseDown={mouseDownHandlerL}
                  onMouseUp={mouseUpHandlerL}>
                {(Leftbuttionpush || props.buttonLDown)&& <img src={img_Leftbuttionpush}/>}
            </div>

            <div className="ButtionA" onClick={clickGameA}>
                { ButtionA && <img src={img_ButtionA}/>}
            </div>

        </div>

    )
}
export default memo(Controls);