
import React ,{ memo} from "react";
import '../../images/FunnyCook/funnyCook.css'

import img_Cooker4 from "../../images/FunnyCook/Cooker4.png";
import img_Cooker4handup from "../../images/FunnyCook/Cooker4handup.png";
import img_Cooker4handdown from "../../images/FunnyCook/Cooker4handdown.png";
import img_Cooker3 from "../../images/FunnyCook/Cooker3.png";
import img_Cooker3handup from "../../images/FunnyCook/Cooker3handup.png";
import img_Cooker3nanddown from "../../images/FunnyCook/Cooker3nanddown.png";
import img_Cooker2 from "../../images/FunnyCook/Cooker2.png";
import img_Cooker2handup from "../../images/FunnyCook/Cooker2handup.png";
import img_Cooker2nanddown from "../../images/FunnyCook/Cooker2nanddown.png";
import img_Cooker1 from "../../images/FunnyCook/Cooker1.png";
import img_Cooker1handup from "../../images/FunnyCook/Cooker1handup.png";
import img_Cooker1handdown from "../../images/FunnyCook/Cooker1handdown.png";

function Cooker(cooker, up , start) {

    if (start){
        const obj = {}
        obj.Cooker1 = true
        obj.Cooker1_hand_up = false
        obj.Cooker1_hand_down = true

        obj.Cooker2 = true
        obj.Cooker2_hand_up = false
        obj.Cooker2_hand_down = true

        obj.Cooker3 = true
        obj.Cooker3_hand_up = false
        obj.Cooker3_hand_down = true

        obj.Cooker4 = true
        obj.Cooker4_hand_up = false
        obj.Cooker4_hand_down = true
        return obj
    }

    const obj = {}
    obj.Cooker1 = false
    obj.Cooker1_hand_down = false

    obj.Cooker2 = false
    obj.Cooker2_hand_down = false

    obj.Cooker3 = false
    obj.Cooker3_hand_down = false

    obj.Cooker4 = false
    obj.Cooker4_hand_down = false



    switch (cooker) {
        case 1: obj.Cooker1 = true;

            obj.Cooker1_hand_up = up===1?true:false;
            obj.Cooker1_hand_down = up===1?false:true;

            break;
        case 2: obj.Cooker2 = true
            obj.Cooker2_hand_down = up===2?false:true;
            obj.Cooker2_hand_up = up===2?true:false;
            break;

        case 3: obj.Cooker3 = true
            obj.Cooker3_hand_down = up===3?false:true;
            obj.Cooker3_hand_up = up===3?true:false;
            break;

        case 4: obj.Cooker4 = true
            obj.Cooker4_hand_down = up===4?false:true;
            obj.Cooker4_hand_up = up===4?true:false;
            break;
        default:
            break;
    }

    return obj;

}

function Player(props){

    // console.log("render Player")
    const obj = Cooker(props.posgame, props.up, props.start)




    return (
        <>
            {obj.Cooker4 && <div className="Cooker4"><img src={img_Cooker4} alt={"img_Cooker4"}/></div>}
            {obj.Cooker4_hand_up && <div className="Cooker4handup"><img src={img_Cooker4handup}/></div>}
            {obj.Cooker4_hand_down && <div className="Cooker4handdown"><img src={img_Cooker4handdown}/></div>}

            {obj.Cooker3 && <div className="Cooker3"><img src={img_Cooker3}/></div>}
            {obj.Cooker3_hand_up && <div className="Cooker3handup"><img src={img_Cooker3handup}/></div>}
            {obj.Cooker3_hand_down && <div className="Cooker3nanddown"><img src={img_Cooker3nanddown}/></div>}

            {obj.Cooker2 && <div className="Cooker2"><img src={img_Cooker2}/></div>}
            {obj.Cooker2_hand_up && <div className="Cooker2handup"><img src={img_Cooker2handup}/></div>}
            {obj.Cooker2_hand_down && <div className="Cooker2nanddown"><img src={img_Cooker2nanddown}/></div>}

            {obj.Cooker1 && <div className="Cooker1"><img src={img_Cooker1}/></div>}
            {obj.Cooker1_hand_up && <div className="Cooker1handup"><img src={img_Cooker1handup}/></div>}
            {obj.Cooker1_hand_down && <div className="Cooker1handdown"><img src={img_Cooker1handdown}/></div>}
        </>
    )
}
export default memo(Player);