import img_Chicken44 from "../../images/FunnyCook/Chicken44.png";
import img_Chicken43 from "../../images/FunnyCook/Chicken43.png";
import img_Chicken42 from "../../images/FunnyCook/Chicken42.png";
import img_Chicken41 from "../../images/FunnyCook/Chicken41.png";
import img_Fish35 from "../../images/FunnyCook/Fish35.png";
import img_Fish34 from "../../images/FunnyCook/Fish34.png";
import img_Fish33 from "../../images/FunnyCook/Fish33.png";
import img_Fish32 from "../../images/FunnyCook/Fish32.png";
import img_Fish31 from "../../images/FunnyCook/Fish31.png";
import img_Sosige25 from "../../images/FunnyCook/Sosige25.png";
import img_Sosige24 from "../../images/FunnyCook/Sosige24.png";
import img_Sosige23 from "../../images/FunnyCook/Sosige23.png";
import img_Sosige22 from "../../images/FunnyCook/Sosige22.png";
import img_Sosige21 from "../../images/FunnyCook/Sosige21.png";
import img_Meat15 from "../../images/FunnyCook/Meat15.png";
import img_Meat14 from "../../images/FunnyCook/Meat14.png";
