import img_Meat12 from "../../images/FunnyCook/Meat12.png";
import img_Meat11 from "../../images/FunnyCook/Meat11.png";

function food(meat, sosige, fish, chiken){
    const obj = {}

    obj.Chicken41 = false
    obj.Chicken42 = false
    obj.Chicken43 = false
    obj.Chicken44 = false
    obj.Chicken45 = false

    obj.Fish31 = false
    obj.Fish32 = false
    obj.Fish33 = false
    obj.Fish34 = false
    obj.Fish35 = false

    obj.Sosige21 = false
    obj.Sosige22 = false
    obj.Sosige23 = false
    obj.Sosige24 = false
    obj.Sosige25 = false

    obj.Meat11 = false
    obj.Meat12 = false
    obj.Meat13 = false
    obj.Meat14 = false
    obj.Meat15 = false

    switch (meat) {
        case 1:
            obj.Meat15 = true;
            break;
        case 2:
            obj.Meat14 = true;
            break;

        case 3:
            obj.Meat13 = true;
            break;

        case 4:
            obj.Meat12 = true;
            break;
        case 5:
            obj.Meat11 = true;
            break;

        case 6:
            obj.Meat12 = true;
            break;
        case 7:
            obj.Meat13 = true;
            break;
        case 8:
            obj.Meat14 = true;
            break;
        case 9:
            obj.Meat15 = true;
            break;





        default:
            break;
    }
    switch (sosige) {
        case 1:
            obj.Sosige25 = true;
            break;
        case 2:
            obj.Sosige24 = true;
            break;
        case 3:
            obj.Sosige23 = true;
            break;
        case 4:
            obj.Sosige22 = true;
            break;
        case 5:
            obj.Sosige21 = true;
            break;

        case 6:
            obj.Sosige22 = true;
            break;
        case 7:
            obj.Sosige23 = true;
            break;
        case 8:
            obj.Sosige24 = true;
            break;
        case 9:
            obj.Sosige25 = true;
            break;



        default:
            break;

    }
    switch (fish) {

        case 6:
            obj.Fish32 = true;
            break;
        case 7:
            obj.Fish33 = true;
            break;
        case 8:
            obj.Fish34 = true;
            break;
        case 9:
            obj.Fish35 = true;
            break;
        case 5:
            obj.Fish31 = true;
            break;
        case 4:
            obj.Fish32 = true;
            break;
        case 3:
            obj.Fish33 = true;
            break;
        case 2:
            obj.Fish34 = true;
            break;
        case 1:
            obj.Fish35 = true;
            break;
        default:
            break;

    }
    switch (chiken) {


        case 9:
            obj.Chicken45 = true;
            break;
        case 8:
            obj.Chicken44 = true;
            break;
        case 7:
            obj.Chicken43 = true;
            break;
        case 6:
            obj.Chicken42 = true;
            break;
        case 5:
            obj.Chicken41 = true;
            break;
        case 4:
            obj.Chicken42 = true;
            break;
        case 3:
            obj.Chicken43 = true;
            break;
        case 2:
            obj.Chicken44 = true;
            break;
        case 1:
            obj.Chicken45 = true;
            break;
        default:
            break;

    }

    return obj;
}

function Food(props){



    const meat = props.meat
    const sosige = props.sosige
    const fish = props.fish
    const chiken = props.chiken


    const obj = food(meat, sosige, fish, chiken)

    return (
        <>
            {obj.Chicken45 && <div className="Chicken45"><img src={img_Chicken45} alt={"img_Chicken45"} /></div>}
            {obj.Chicken44 && <div className="Chicken44"><img src={img_Chicken44}/></div>}
            {obj.Chicken43 && <div className="Chicken43"><img src={img_Chicken43}/></div>}
            {obj.Chicken42 && <div className="Chicken42"><img src={img_Chicken42}/></div>}
            {obj.Chicken41 && <div className="Chicken41"><img src={img_Chicken41}/></div>}
            {obj.Fish35 && <div className="Fish35"><img src={img_Fish35}/></div>}
            {obj.Fish34 && <div className="Fish34"><img src={img_Fish34}/></div>}
            {obj.Fish33 && <div className="Fish33"><img src={img_Fish33}/></div>}
            {obj.Fish32 && <div className="Fish32"><img src={img_Fish32}/></div>}
            {obj.Fish31 && <div className="Fish31"><img src={img_Fish31}/></div>}
            {obj.Sosige25 && <div className="Sosige25"><img src={img_Sosige25}/></div>}
            {obj.Sosige24 && <div className="Sosige24"><img src={img_Sosige24}/></div>}
            {obj.Sosige23 && <div className="Sosige23"><img src={img_Sosige23}/></div>}
            {obj.Sosige22 && <div className="Sosige22"><img src={img_Sosige22}/></div>}
            {obj.Sosige21 && <div className="Sosige21"><img src={img_Sosige21}/></div>}
            {obj.Meat15 && <div className="Meat15"><img src={img_Meat15}/></div>}
            {obj.Meat14 && <div className="Meat14"><img src={img_Meat14}/></div>}
            {obj.Meat13 && <div className="Meat13"><img src={img_Meat13}/></div>}
            {obj.Meat12 && <div className="Meat12"><img src={img_Meat12}/></div>}
            {obj.Meat11 && <div className="Meat11"><img src={img_Meat11}/></div>}
        </>
    )
};

export default memo(Food)